.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
}

/* Typography */
.h1 {
  font-size: 6em;
  font-weight: lighter;
  letter-spacing: -1.5px;
}

.h2 {
  font-size: 3.75em;
  font-weight: lighter;
  letter-spacing: -0.5px;
}

.h3 {
  font-size: 3em;
  letter-spacing: 0px;
}

.h4 {
  font-size: 2.125em;
  letter-spacing: 0.25px;
}

.h5 {
  font-size: 1.5em;
  letter-spacing: 0px;
}

.h6 {
  font-size: 1.25em;
  font-weight: bold;
  letter-spacing: 0.15px;
}

.subtitle1 {
  font-size: 1em;
  letter-spacing: 0.15px;
}

.subtitle2 {
  font-size: 0.875em;
  font-weight: bold;
  letter-spacing: 0.1px;
}

.body1 {
  font-size: 1em;
  letter-spacing: 0.5px;
}

.body2 {
  font-size: 0.875em;
  letter-spacing: 0.25px;
}

.button {
  font-size: 0.875em;
  font-weight: bold;
  letter-spacing: 1.25px;
  text-transform: uppercase;
}

.caption {
  font-size: 0.75em;
  letter-spacing: 0.4px;
}

.overline {
  font-size: 0.625em;
  letter-spacing: 1.5px;
  text-transform: uppercase;
}

/* Debugging */
.border1 {
  border: 1px solid blue;
}

.border2 {
  border: 1px solid red;
}